.contacts {
  display: grid;
  grid-auto-columns: 1
}
.contact {
  text-align: left;
  margin: 20px;
  flex: 1;
  transition: .3s
}
#icon-delete {
  position: absolute;
  top: 10px;
  right: 10px
}
#icon-face{
  font-size: 40px
}
@media screen and (max-width: 700px)
{
  .contacts {
    flex-direction: column;
  }
}
