* {
  transition: .1s
}
.App {
  position: absolute;
  width: 100%;
  height: 100%;
  word-break: break-all;
  overflow-y: scroll;
  padding-bottom: 100px
}
.options {
  position:fixed;
  bottom: 0px;
  width: 30%;
  border-radius: 10px;
  box-shadow: 0 0 10px black;
  margin: 10px;
}
@media screen and (max-width: 700px) {
  .options {
    width:auto;
    display: flex;
    .NavBar span {
      float: left;
      margin-left: 10px
    }
  }

}
